import {
  predict,
  warn,
  type PredictCoreApi,
  type UserProfile,
} from '@algolia/predict-core';
import { dequal } from 'dequal/lite';
import React, {
  ReactNode,
  useCallback,
  useEffect,
  useRef,
  useState,
} from 'react';
import { useSyncExternalStore } from 'use-sync-external-store/shim';

import { PredictContext, PredictReactApi } from './lib/PredictContext';

export type PredictProps = {
  userProfile: UserProfile;
  suppressExperimentalWarning?: boolean;
  children: ReactNode;
};

export function Predict({
  userProfile,
  suppressExperimentalWarning = false,
  children,
}: PredictProps) {
  const predictRef = useRef<PredictCoreApi | null>(null);

  if (predictRef.current === null) {
    predictRef.current = predict({ userProfile });
  }

  const [predictApi, setPredictApi] = useState<PredictReactApi>(() => ({
    userProfile: predictRef.current!.getUserProfile(),
    affinities: predictRef.current!.getAffinities(),
    funnelStage: predictRef.current!.getFunnelStage(),
    orderValue: predictRef.current!.getOrderValue(),
    cartAbandonment: predictRef.current!.getCartAbandonment(),
    segments: predictRef.current!.getSegments(),
    properties: predictRef.current!.getProperties(),
    zones: predictRef.current!.getZones(),
    addZone: predictRef.current!.addZone,
    removeZone: predictRef.current!.removeZone,
    updateZone: predictRef.current!.updateZone,
    subscribe: predictRef.current!.subscribe,
  }));

  const [prevUserProfile, setPrevUserProfile] = useState(() =>
    predictRef.current!.getUserProfile()
  );
  if (!dequal(userProfile, prevUserProfile)) {
    predictRef.current.update({ userProfile });
    setPrevUserProfile(userProfile);
  }

  const store = useSyncExternalStore(
    useCallback(
      () =>
        predictRef.current!.subscribe(({ eventType, ...state }) => {
          // We manage zone updates directly with the React state, so we don't
          // need to listen to this event.
          if (eventType !== 'ZONE_UPDATED') {
            setPredictApi((prev) => ({ ...prev, ...state }));
          }
        }),
      []
    ),
    () => predictApi,
    () => predictApi
  );

  useEffect(() => {
    warn(
      suppressExperimentalWarning,
      'This version is experimental and not production-ready.\n\n' +
        'Please report any bugs at https://github.com/algolia/predict/issues/new\n\n' +
        '(To disable this warning, pass `suppressExperimentalWarning` to <Predict>.)'
    );
  }, [suppressExperimentalWarning]);

  return (
    <PredictContext.Provider value={store}>{children}</PredictContext.Provider>
  );
}
