import { invariant } from '@algolia/predict-core';
import { useContext } from 'react';

import { PredictContext } from './PredictContext';

export function usePredictContext({
  errorMessage = 'The Predict APIs must be used within the <Predict> provider.',
} = {}) {
  const context = useContext(PredictContext);

  invariant(context !== null, errorMessage);

  return context;
}
