import { createZone, type Zone } from '@algolia/predict-core';
import React, {
  type ComponentProps,
  type ReactNode,
  useCallback,
  useMemo,
  useState,
} from 'react';
import { useSyncExternalStore } from 'use-sync-external-store/shim';

import { PredictZoneContext } from './lib/PredictZoneContext';
import { usePredictContext } from './lib/usePredictContext';
import { type PredictApi, usePredict } from './usePredict';

export type PredictZoneProps = Omit<
  ComponentProps<'div'>,
  'onMouseDown' | 'onTouchStart'
> & {
  id?: Zone['id'];
  name: Zone['name'];
  segment?: string | ((api: PredictApi) => boolean);
  children: ReactNode;
  fallback?: ReactNode;
};

export function PredictZone({
  id,
  children,
  name,
  segment,
  fallback = null,
  ...props
}: PredictZoneProps) {
  const { addZone, removeZone, updateZone } = usePredictContext({
    errorMessage:
      'The <PredictZone> must be mounted within the <Predict> provider.',
  });
  const predictApi = usePredict();
  const [zone, setZone] = useState(() => {
    const active =
      // eslint-disable-next-line no-nested-ternary
      typeof segment === 'function'
        ? segment(predictApi)
        : segment
        ? Boolean(predictApi.segments.find((s) => s.name === segment))
        : true;
    return createZone({ id, name, active });
  });
  const [isDismissed, setIsDismissed] = useState(false);

  const [prevName, setPrevName] = useState(zone.name);
  if (name !== prevName) {
    setPrevName(name);
    updateZone({ id: zone.id, name });
  }

  if (!isDismissed) {
    const active =
      // eslint-disable-next-line no-nested-ternary
      typeof segment === 'function'
        ? segment(predictApi)
        : segment
        ? Boolean(predictApi.segments.find((s) => s.name === segment))
        : zone.active;

    if (active !== zone.active) {
      setZone((prev) => ({ ...prev, active }));
      updateZone({ id: zone.id, active });
    }
  }

  const zoneApi = useMemo(
    () => ({
      dismissZone: () => {
        setZone((prev) => ({ ...prev, active: false, happened: true }));
        updateZone({ id: zone.id, active: false, happened: true });
        setIsDismissed(true);
      },
    }),
    [updateZone, zone.id]
  );

  const zoneStore = useSyncExternalStore(
    useCallback(() => {
      addZone(zone);

      return () => {
        removeZone(zone);
      };
      // We don't track zone changes because we update the zone in the store via
      // `updateZone()`.
      // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [addZone, removeZone]),
    () => zoneApi,
    () => zoneApi
  );

  if (!zone.active) {
    return <>{fallback}</>;
  }

  return (
    <PredictZoneContext.Provider value={zoneStore}>
      <div
        {...props}
        data-predictzoneid={zone.id}
        onMouseDown={() => {
          setZone((prev) => ({ ...prev, happened: true }));
          updateZone({ id: zone.id, happened: true });
        }}
        onTouchStart={() => {
          setZone((prev) => ({ ...prev, happened: true }));
          updateZone({ id: zone.id, happened: true });
        }}
      >
        {children}
      </div>
    </PredictZoneContext.Provider>
  );
}
