import {
  type Affinity,
  type FunnelStage,
  type OrderValue,
  type PredictCoreApi,
  type Property,
  type Segment,
  type UserProfile,
  type Zone,
} from '@algolia/predict-core';
import { createContext } from 'react';

export type PredictReactApi = Omit<
  PredictCoreApi,
  | 'getAffinities'
  | 'getFunnelStage'
  | 'getOrderValue'
  | 'getCartAbandonment'
  | 'getSegments'
  | 'getProperties'
  | 'getUserProfile'
  | 'getZones'
  | 'update'
> & {
  userProfile: UserProfile;
  affinities: Affinity[];
  funnelStage: FunnelStage[];
  orderValue: OrderValue | undefined;
  cartAbandonment: number;
  segments: Segment[];
  properties: Property[];
  zones: Zone[];
};

export const PredictContext = createContext<PredictReactApi | null>(null);

if (__DEV__) {
  PredictContext.displayName = 'Predict';
}
