import { invariant } from '@algolia/predict-core';
import { useContext } from 'react';

import { PredictZoneContext } from './lib/PredictZoneContext';

export function usePredictZone() {
  const context = useContext(PredictZoneContext);

  invariant(
    context !== null,
    '`usePredictZone()` must be used within a <PredictZone>.'
  );

  return context;
}
