import { type PredictReactApi } from './lib/PredictContext';
import { usePredictContext } from './lib/usePredictContext';
import { useStableValue } from './lib/useStableValue';

export type PredictApi = Omit<
  PredictReactApi,
  'addZone' | 'removeZone' | 'updateZone' | 'subscribe'
>;

export function usePredict(): PredictApi {
  const { addZone, removeZone, updateZone, subscribe, ...rest } =
    usePredictContext({
      errorMessage:
        '`usePredict()` must be used within the <Predict> provider.',
    });
  const predictApi = useStableValue(rest);

  return predictApi;
}
